.l-wrapper {
  flex: 1 0 auto;
  overflow: hidden; }

.l-header {
  z-index: 10; }

.l-main {
  z-index: 5; }

.l-footer {
  overflow: hidden; }

.l-container {
  max-width: 960px;
  margin: 0 auto; }
  .l-container:before {
    content: "";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
    line-height: 0;
    *zoom: 1; }
  .l-container:after {
    content: "";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
    line-height: 0;
    *zoom: 1; }
  @media (max-width: 1000px) {
    .l-container {
      width: auto;
