body{
    position: fixed;
    width: 100%;
}
.header {
    background: rgba(#000, 0.15);
    transform-origin: center;
    padding: 1px 20px 20px;

    @media (max-width: 768px) {
        padding: 1px 20px 1px;
    }
    
    h1{
        text-align: center;
    }

    p{
        margin-top: -10px;
        text-align: center;
    }
}

.main {
    transform-origin: center;
}

.footer {
    padding: 25px 20px 20px;
    background: rgba(#000, 0.75);
    transform-origin: center;
}
.copywrite{
    margin: 0;
    text-align: center;
    color: #fff;

    a{
        color: #fff;
        text-decoration: none;
    }
}

.link-github{
    margin-right: 20px;
    transition: opacity $translate-time $translate-ease;

    &:hover{
        opacity: .8;
    }
    
    &:before{
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 32px;
        height: 32px;
        margin-right: 10px;
        position: relative;
        top: -4px;
        background-image: url(data:image/svg+xml;utf8;base64,);
    }
}


.jelly-text-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 130px;
    margin-bottom: 130px;
    background: rgba(#000, 0.05); // transition: background .3s ease-out;
    &:hover {
        // background: rgba(rgb(0, 26, 255), 0.05);
    }
    @media (max-width: 1024px) {
        margin-top: 90px;
        margin-bottom: 90px;
    }
    @media (max-width: 768px) {
        margin-top: 50px;
        margin-bottom: 50px;
    }
    &.-left {
        flex-direction: row;
        padding-right: 3%;
        @media (max-width: 768px) {
            padding-right: 0;
        }
    }
    &.-right {
        flex-direction: row-reverse;
        padding-left: 3%;

        @media (max-width: 768px) {
            padding-left: 0;
        }
    }
    &__image {
        display: block;
        position: relative;
        width: 33%;

        @media (max-width: 768px) {
            width: 100%;
        }

        span,
        a {
            position: relative;
            display: block;
            height: 0;
            padding-bottom: 100%;
        }
        img {
            position: absolute;
            left: 0;
            top: 0;
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__text {
        display: block;
        position: relative;
        width: 63%;

        @media (max-width: 768px) {
            width: 100%;
            padding: 1px 30px 20px;
        }
    }
}